body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: black;
  background-color: #f2f2f2;
}


h1 {
    color: #8B4513;
    text-align: center;
}

.search {
    display: -ms-flexbox;
    display: flex;
    margin: 90px auto 25px;
    border: none;
    padding: 15px 15px 15px 30px;
    width: 30%;
    font-size: 26px;
    text-align: center;
    color: #8b4513;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    position: relative;
    top: 0px;
    left: 0px;
}

.search:focus {
    -webkit-box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.2);
    top: -5px;
    left: -5px;
    outline: none;
}

.post-list {
    padding: 0;
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    max-width: 100%;
    position: relative;
}

.post-card {
    width: 33%;
    -ms-flex-preferred-size: content;
        flex-basis: content;
}

.post-card a {
    color: inherit;
    text-decoration: none;
    display: block;
    padding: 10px;
    margin: 10px;
    background-color: #fff;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    position: relative;
    top: 0px;
    left: 0px;
}


.post-card a:hover {
    -webkit-box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.2);
    top: -5px;
    left: -5px;
}

.post-card a h3 {
    text-align: center;
    color: #8B4513;
    text-transform: uppercase;
}

.but {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.but button {
    color: #8B4513;
    font-size: 20px;
    text-transform: uppercase;
    background-color: white;
    padding: 15px 30px;
    margin: 20px;
    border: none;
    position: relative;
    top: 0px;
    left: 0px;
}

.but button:hover {
    cursor: pointer;
    background-color: #fff;
    -webkit-box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.2);
    top: -5px;
    left: -5px;
}

.but button:focus {
    outline: none;
}


